.case {
  background-color: #fff;
  padding-top: 83px;
  padding-bottom: 26px;

  &_wrap {
    max-width: 980px;
    margin: 0 auto;
  }

  &_item {
    flex-basis: 25%;
    margin-right: 42px;

    &:last-child { margin-right: 0; }

    // &:nth-child(2) { margin-right: 55px; }
  }

  &_box {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    align-content: flex-start;
  }

  &_icon {
    min-height: 75px;
    display: flex;
    align-items: flex-start;
    align-content: flex-start;
    justify-content: flex-start;
    margin-bottom: 43px;
  }

  &_head,
  &_head h3 {
    font-family: 'TT Prosto Sans';
    font-size: 16px;
    line-height: 19px;
    font-weight: bold;
    color: #6B797F;
    margin: 0;
    padding: 0;
  }

  &_head {
    margin-bottom: 21px;
  }

  &_description {
    font-family: 'TT Prosto Sans';
    color: #6B797F;
    font-size: 14px;
    line-height: 22px;

    p:last-child { margin-bottom: 0; }
  }
}

.cases {
  display: flex;
  align-items: stretch;
  align-content: stretch;

  &-center {
    justify-content: center;
  }
}

.case_switch {
  list-style-type: none;
  margin: 0;
  padding: 0;
  color: #A6A19F;
  font-family: 'TT Prosto Sans';
  font-size: 16px;
  line-height: 16px;
  font-weight: 400;
  text-transform: lowercase;
  display: flex;
  align-items: center;
  align-content: center;
  justify-content: center;
  margin-bottom: 42px;

  .tabs__menu-item {
    margin-right: 2px;
  }

  .tabs__menu-item.active {

    .case_switch_item {
      background-color: #6B797F;
      color: #ffffff;
      font-weight: bold;
    }
  }

  &_item {
    color: #A6A19F;
    margin: 0;
    border-radius: 3px;
    background-color: transparent;
    padding: 7px 11px 7px 11px;
    border: 1px solid transparent;

    &:hover {
      border-color: #6B797F;
      color: #A6A19F;
    }
  }
}

.case_step {
  display: flex;
  align-items: center;
  align-content: center;
  justify-content: center;
  margin-bottom: 30px;

  &_item {
    padding: 15px 19px;
    font-family: 'TT Prosto Sans';
    font-size: 16px;
    line-height: 16px;
    font-weight: bold;
    color: #6B797F;
    text-transform: lowercase;
    background-color: transparent;
    border-radius: 5px;
    border: 1px solid transparent;
    height: 86px;
  }

  &_info {
    border-color: #C0C8CB;
    position: relative;
    margin-right: 27px;
    flex-basis: 100%;

    &:after,
    &:before {
      content: '';
      display: block;
      position: absolute;
      left: 100%;
      width: 0;
      height: 0;
      border-style: solid;
    }

    &:before {
      top: 1px;
      border-color: transparent transparent transparent #C0C8CB;
      border-width: 41px 0 41px 25px;
    }

    &:after {
      top: 3px;
      border-color: transparent transparent transparent #ffffff;
      border-width: 39px 0 39px 24px;
    }
  }

  &_help {
    flex-basis: 330px;
    min-width: 330px;
    background-color: #F2F2F2;
    border-color: #F2F2F2;
  }
}
